	module.exports = {"data":{"challengeNode":{"title":"Animate Multiple Elements at Variable Rates","description":["在前面的关卡里，你通过改变<code>@keyframes</code>改变了两个相似动画元素的频率。你也可以通过改变多个元素的<code>animation-duration</code>来达到同样的效果。","在编辑器代码运行的动画里，天空中有三个以同样频率不停的闪烁的星星。你可以设置每一个星星的<code>animation-duration</code>属性为不同的值来使其具有不同的闪烁频率。","<hr>","依次设置 class 为<code>star-1</code>、<code>star-2</code>、<code>star-3</code>的<code>animation-duration</code>为 1s、0.9s、1.1s。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/applied-visual-design/animate-multiple-elements-at-variable-rates","blockName":"Applied Visual Design","tests":[{"text":"class 为<code>star-1</code>的<code>animation-duration</code>属性值应该 1s。","testString":"assert($('.star-1').css('animation-duration') == '1s', 'class 为<code>star-1</code>的<code>animation-duration</code>属性值应该 1s。');"},{"text":"class 为<code>star-2</code>的<code>animation-duration</code>属性值应该 0.9s。","testString":"assert($('.star-2').css('animation-duration') == '0.9s', 'class 为<code>star-2</code>的<code>animation-duration</code>属性值应该 0.9s。');"},{"text":"class 为<code>star-3</code>的<code>animation-duration</code>属性值应该 1.1s。","testString":"assert($('.star-3').css('animation-duration') == '1.1s', 'class 为<code>star-3</code>的<code>animation-duration</code>属性值应该 1.1s。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  .stars {\n    background-color: white;\n    height: 30px;\n    width: 30px;\n    border-radius: 50%;\n    animation-iteration-count: infinite;\n  }\n\n  .star-1 {\n    margin-top: 15%; \n    margin-left: 60%;\n    animation-duration: 1s;\n    animation-name: twinkle;\n  }\n\n  .star-2 {\n    margin-top: 25%;\n    margin-left: 25%;\n    animation-duration: 1s;\n    animation-name: twinkle;\n  }\n\n  .star-3 {\n    margin-top: 10%;\n    margin-left: 50%;\n    animation-duration: 1s;\n    animation-name: twinkle;\n  }\n\n  @keyframes twinkle {\n    20% {\n      transform: scale(0.5);\n      opacity: 0.5;\n    }\n  }\n\n  #back {\n    position: fixed;\n    padding: 0;\n    margin: 0;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: linear-gradient(black, #000099, #66c2ff, #ffcccc, #ffeee6);\n  }\n</style>\n\n<div id=\"back\"></div>\n<div class=\"star-1 stars\"></div>\n<div class=\"star-2 stars\"></div>\n<div class=\"star-3 stars\"></div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/applied-visual-design/change-animation-timing-with-keywords","id":"587d78a8367417b2b2512ae6"},"slug":"/responsive-web-design/applied-visual-design/animate-multiple-elements-at-variable-rates"}}

